body {
  background-image: url("/src/bg.png");
  background-attachment: fixed;
  background-size: cover;
  backdrop-filter: blur(2px);
  padding: 10px;
}
.App {
  font-family: sans-serif;
  text-align: center;
  background-color: black;
  color: #fff;
  padding: 7px;
  max-width: 320px;
  margin: auto;
  border-radius: 25px;
  box-shadow: 4px 8px 15px #000;
}
.App-orange {
  background-color: #ec8922;
}
a{
  color:#d3d3d3;
  text-decoration: none;
}
#orange {
  height: 60px;
  width: 60px;
  border-radius: 25px;
  background-color: #ec8922;
}
.App-navy {
  background-color: #002b57;
}
#navy {
  height: 60px;
  width: 60px;
  border-radius: 25px;
  background-color: #002b57;
}
#purple {
  height: 60px;
  width: 60px;
  border-radius: 25px;
  background-color: #211226;
}
.App-purple {
  background-color: #211226;
}
#black {
  height: 60px;
  width: 60px;
  border-radius: 25px;
  background-color: #000;
}
P {
  color: #979797;
}
#display {
  background-color: #5e7a57;
  height: 70px;
  margin-bottom: 25px;
  color: #2a4124;
  padding: 5px;
  font-size: 1.5rem;
  box-shadow: inset 4px 10px 15px #455f3e;
}
#input-display,
#output-display {
  width: 98%;
  background-color: #5e7a57;
  outline: none;
  border: none;
  color: #2a4124;
  font-size: 1.5rem;
}
.Button-container {
  border: 1px solid #fff;
  padding: 5px;
  border-radius: 25px;
  padding: 10px;
}
.Button-container button {
  font-size: 3rem;
  margin: 2px;
  width: 70px;
  border-radius: 5px;
}
.Button-container button:hover {
  box-shadow: inset 2px 4px 10px #3f3c3c;
}
#clear {
  background-color: brown;
  color: #fff;
  width: 50%;
}
/* id="multiply", id="divide" */
#multiply,
#devide,
#decimal {
  width: 36%;
}

#decimal {
  background-color: #002b57;
  color: #fff;
}
#equals {
  width: 120px;
}
